html, body {
  margin: 0;
  padding: 0;
  width: 100% auto;
  background color: #00286d;
  background-image: url("/images/nightsky_1200x1100.png");
  background-repeat:horizontal;
  background-size: auto;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}
.App {
  text-align: center;
}

.App-header {
  background-color: #28c8dc;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-link {
  color: #28c8dc;
}

p {
  font-size: 18px;
}

.card {
  height: 200px;
  width: 200px;
  margin: 0.55rem;
  float: left;
}

.img-container {
  height: 100%;
  width: 100%;
  overflow: hidden;
  text-align: center;
  border: 4px solid #28c8dc;
  border-radius: 25px;
  margin: 20px 40px;
}
.wrapper {
  min-height: 100%;
  padding-bottom: 100px;
}

.scoreboard {
  padding-top: 50px;
  background: #28c8dc;
  color: black;
  font-size: 30px;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

